body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.separator{
  padding: 20px;
}
.Assignment{
  position: relative;
  display: table;
  width: auto;
  margin: 0px;
}

.inliner{
  display: inline-block;
  margin: 10px;
}
.main{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: 'g0 g1';}

.register_panel{
  grid-area: g0;
  margin-top: 10%;
  margin-left: 20%;
  height: 20%;
  width: 40%;
}
.login_panel{
  grid-area: g1;
  margin-top: 10%;
  margin-right: 20%;
  height: 20%;
  width: 40%;
}

.Footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.Grades-Container{
  min-height: 85vh;
  padding-top: 50px;
  padding-right: 30px;
  padding-left: 80px;
}
